import Swiper, { Navigation, Pagination } from 'swiper';

let Next = document.querySelector('.slider-next');
let Prev = document.querySelector('.slider-prev');
let Slides = document.querySelectorAll('.partners__slider .swiper-slide');

let swiper = new Swiper('.partners__slider', {
  modules: [Navigation, Pagination],
  loop: false,
  slidesPerView: 1,
  spaceBetween: 0,
  autoHeight: true,
  on: {
    slideChange: function (target) {
      console.log(target.activeIndex)
      if (target.activeIndex > 0) {
        Prev.classList.remove('--disabled');
      } else {
        Prev.classList.add('--disabled');
      }


      if (target.activeIndex == (Slides.length - 1)) {
        Next.classList.add('--disabled');
      } else {
        Next.classList.remove('--disabled');
      }
    },
  },
});




if (Next && Prev) {
  Next.addEventListener('click', () => {
    swiper.slideNext();
  })
  Prev.addEventListener('click', () => {
    swiper.slidePrev();
  })
}