import IMask from 'imask';

var element = document.querySelectorAll('.js-mask-tel');
var maskOptions = {
  mask: '+{7} (000) 000-00-00'
};

if (element) {
  element.forEach(elem => {
    var mask = IMask(elem, maskOptions);
  })
}



